/* eslint-disable */
import { http } from '../http_service'

// Inventario
export function getInventories() {
    return http().get('/store_inventory')
}

export function getInventoryCodes() {
    return http().get('/inventory_codes')
}

export function addInventory(data) {
    return http().post('/store_inventory', data)
}

export function activateDesactivate(id) {
    return http().post(`/store_inventory/${id}`)
}

export function getInventoryById(id) {
    return http().get(`/store_inventory/${id}`)
}

export function updateInventory(id, data) {
    return http().put(`/store_inventory/${id}`, data)
}

// Entradas Inventario
export function getInventoryEntries() {
    return http().get('/store_inventory_entries')
}

export function saveInventoryEntry(data) {
    return http().post('/store_inventory_entry', data)
}

export function deleteInventoryEntry(id) {
    return http().post(`/store_inventory_entry_delete/${id}`)
}