 <!-- eslint-disable -->
<template>
  <b-card title="Agregar Entradas Inventario">
    <!-- Componente para mostrar errores -->
    <ErrorValidation :errors="validationErrors" v-if="validationErrors"></ErrorValidation>
    <div>
      <b-button
        variant="primary"
        class="mb-2"
        @click="$router.go(-1)">       
         Regresar 
      </b-button>
      <validation-observer 
        ref="form_inventory_entry" 
        #default="{invalid}">
        <b-form
          ref="formInventoryEntry"
          :style="{height: trHeight}"
          class="repeater-form"
          @submit.prevent="repeateAgain"
        >

          <!-- Row Loop -->
          <b-row
            v-for="(item, index) in items"
            :id="item.id"
            :key="item.id"
            ref="row"
          >

            <!-- Code -->
            <b-col md="6">
              <validation-provider 
                #default="{ errors }" 
                name="Código del Inventario" 
                rules="required">
                <b-form-group 
                  label="Buscar por Código y Referencia del Inventario" 
                  label-for="inventoryCode"
                  :state="errors.length > 0 ? false:null">
                  <v-select v-model="item.inventory_id" label="codeRef"
                      :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'" :options="inventoryCodes"
                      :reduce="inventoryCodes => inventoryCodes.id" :clearable="true"
                      input-id="inventoryCode" placeholder="Seleccione el código" />
                  <b-form-invalid-feedback :state="errors.length > 0 ? false:null">
                      {{ errors[0] }}
                  </b-form-invalid-feedback>
                </b-form-group>
              </validation-provider>            
            </b-col>
                       
            <!-- Quantity -->           
            <b-col md="2">
              <b-form-group
                label="Cantidad"
                label-for="quantity"
                >
               <validation-provider
                  #default="{ errors }"
                  name="cantidad"
                  rules="required|numeric"
                  >
                  <b-form-input
                    id="quantity"
                    type="text"
                    v-model="item.quantity"
                    :state="errors.length > 0 ? false:null"
                  />
                 <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>
           
            <!-- Remove Button -->
            <b-col
              lg="2"
              md="3"
              class="mb-50"
            >
              <b-button
                v-ripple.400="'rgba(234, 84, 85, 0.15)'"
                variant="outline-danger"
                class="mt-0 mt-md-2"
                @click="removeItem(index)"
              >
                <feather-icon
                  icon="XIcon"
                  class="mr-25"
                />
                <span>Eliminar</span>
              </b-button>
            </b-col>
            <b-col cols="12">
              <hr>
            </b-col>
          </b-row>

          <!-- Boton Agregar Campos -->
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            class="mr-2 my-1"
            variant="secondary"
            @click="repeateAgain"
          >
            <feather-icon
              icon="PlusIcon"
              class="mr-25"
            />
            <span>Agregar Campos</span>
          </b-button>  

          <!-- Boton Registrar -->
          <b-button
            class="my-1"
            variant="success"
            :disabled="invalid"
            @click="saveInventoryEntry"
          >
            <feather-icon
              icon="CheckIcon"
              class="mr-25"
            />
            <span>Registrar Entradas</span>
          </b-button>

        </b-form>       
      </validation-observer>
    </div>     
  </b-card>
</template>

<script>

/*eslint-disable */

import {
  BForm, BFormGroup, BFormInput, BRow, BCol, BButton, BCard,
} from 'bootstrap-vue'
import { heightTransition } from '@core/mixins/ui/transition'
import Ripple from 'vue-ripple-directive'

// VSelect
import vSelect from 'vue-select'

// Servicios
import * as inventoryService from '@/services/store/store'

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
  components: {
    BForm,
    BRow,
    BCol,
    BButton,
    BFormGroup,
    BFormInput,
    BCard,
    vSelect,
  },
  directives: {
    Ripple,
  },
  mixins: [heightTransition],
  data() {
    return {
      validationErrors: '',
      inventoryCodes : [],
      items: [{
        id: 1,
        inventory_id : '',
        quantity : '',
      }],
      nextInventoryEntryId: 1,
    }
  },

  mounted() {
    this.initTrHeight()
  },

  created() {
    window.addEventListener('resize', this.initTrHeight)
    this.getInventoryCodes()
  },

  destroyed() {
    window.removeEventListener('resize', this.initTrHeight)
  },

  methods: {

    repeateAgain() {
      this.items.push({
        id: this.nextInventoryEntryId += this.nextInventoryEntryId,
      })

      this.$nextTick(() => {
        this.trAddHeight(this.$refs.row[0].offsetHeight)
      })
    },

    removeItem(index) {
      this.items.splice(index, 1)
      this.trTrimHeight(this.$refs.row[0].offsetHeight)
    },

    initTrHeight() {
      this.trSetHeight(null)
      this.$nextTick(() => {
        this.trSetHeight(this.$refs.form_inventory_entry.scrollHeight)
      })
    },

    async saveInventoryEntry() {
      try {

        // Valida si el formulario es correcto
        const isValid = await this.$refs.form_inventory_entry.validate();
        if(!isValid) {
          this.showToast('Alerta', 'AlertTriangleIcon', 'Validar el formulario', 'warning') 
        }else {
          const response = await inventoryService.saveInventoryEntry(this.items) 

          if(response.status === 200) {
            this.validationErrors = ''
            this.$refs.formInventoryEntry.reset()
            this.showToast('Agregado', 'CheckIcon', 'Entradas del Inventario Agregadas', 'success')
            
            setTimeout(() => {
              this.$router.go(-1)
            }, 3000)
          }
        }        
      } catch (error) {
          if (error.response.status === 422){
            this.validationErrors = error.response.data.errors;
          }          
      }
    },

    async getInventoryCodes(){
      try {
        const response = await inventoryService.getInventoryCodes()        

        if(response.status === 200) {
          this.inventoryCodes = response.data.data
        }
      } catch (error) {
        if (error.response.status === 422){
          const errorInfo = error.response.data.errors;
          this.showToast('Advertencia!', 'AlertCircleIcon', errorInfo, 'warning')
        }
      }
    },
    
    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },
}
</script>

<style lang="scss" scoped>
.repeater-form {
  // overflow: hidden;
  transition: .35s height;
}
</style>
